import { AddCircleOutlineOutlined } from '@mui/icons-material';
import { Box, Button, Grid, Typography } from '@mui/material';
import { IncomeStatementApi, InitIncomeStatementApi } from 'apis/Reports';
import useAuth from 'hooks/useAuth';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { gridSpacing } from 'store/constant';
import MainCard from 'ui-component/cards/MainCard';
import { apiErrorSnackBar } from 'utils/SnackBar';
import CenterDialog from 'views/utilities/CenterDialog';
import GenerateForm from './GenerateForm';
import ReportPage from './ReportPage';

const Index = () => {
    const { company, recall } = useAuth();
    const [openGenerate, setOpenGenerate] = useState(false);
    const [reportData, setReportData] = useState({});
    const [initData, setInitData] = useState({});

    const generateReport = () => {
        setOpenGenerate((prevState) => !prevState);
    };

    const submitHandler = () => {
        setOpenGenerate((prevState) => !prevState);
    };

    useEffect(() => {
        InitIncomeStatementApi()
            .then((res) => {
                setInitData(res.data.data);
            })
            .catch(() => {
                apiErrorSnackBar(err);
            });
    }, []);

    useEffect(() => {
        const values = {
            company_id: company.company_id,
            start_date: moment(company.start_date).format('YYYY-MM-DD'),
            end_date: moment(company.end_date).format('YYYY-MM-DD')
        };

        IncomeStatementApi(values)
            .then((res) => {
                setReportData(res.data.data);
            })
            .catch((err) => {
                apiErrorSnackBar(err);
            });
    }, [recall]);

    return (
        <>
            <MainCard
                title={
                    <Grid container alignItems="center" spacing={gridSpacing} sx={{ mb: -1, mt: -3 }}>
                        <Grid item xs={12}>
                            <Grid container alignItems="center" spacing={gridSpacing}>
                                <Grid item sx={{ flexGrow: 1 }}>
                                    <Typography variant="column">Income Statement</Typography>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" onClick={() => generateReport()}>
                                        <AddCircleOutlineOutlined sx={{ mr: 0.5 }} /> Generate Report
                                    </Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                }
                content={true}
            >
                {Object.keys(reportData).length > 0 ? (
                    <ReportPage reportData={reportData} />
                ) : (
                    <Box component="span">
                        <Typography
                            variant="h2"
                            textAlign="center"
                            sx={{
                                p: '10%'
                            }}
                            color="GrayText"
                        >
                            Please Generate Report
                        </Typography>
                    </Box>
                )}
            </MainCard>
            {openGenerate && (
                <CenterDialog
                    title={`Generate Income Statement Report`}
                    open={openGenerate}
                    onClose={() => setOpenGenerate((prevState) => !prevState)}
                    id="generateForm"
                >
                    <GenerateForm
                        value={reportData}
                        formID="generateForm"
                        initData={initData}
                        setReportData={setReportData}
                        onSubmit={submitHandler}
                    />
                </CenterDialog>
            )}
        </>
    );
};

export default Index;
